<template>
  <div class="container">
    <div class="header">
      <div class="title">编辑用户</div>
      <div class="back" @click="hanleHide">
        <fa-icon icon-name="undo" />
        返回
      </div>
    </div>
    <el-divider />
    <div class="wrap">
      <el-row>
        <el-col :lg="16" :md="20" :sm="24" :xs="24">
          <el-form ref="editForm" v-loading="loading" :model="form" status-icon label-width="100px" @submit.native.prevent>
            <el-form-item label="用户权限" prop="auth">
              <el-select v-model="form.auth" placeholder="请选择">
                <el-option
                  v-for="item in 4"
                  :key="item"
                  :label="item"
                  :value="item">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="用户名" prop="username">
              <el-input v-model="form.username" size="medium" placeholder="请填写用户名"/>
            </el-form-item>
            <el-form-item label="密码" prop="password">
              <el-input v-model="form.password" size="medium" placeholder="请填写密码"/>
            </el-form-item>
            <el-form-item label="真实姓名" prop="realname">
              <el-input v-model="form.realname" size="medium" placeholder="请填写真实姓名"/>
            </el-form-item>
            <el-form-item label="手机号" prop="mobile">
              <el-input v-model="form.mobile" size="medium" placeholder="请填写手机号"/>
            </el-form-item>
            <el-form-item label="邮箱" prop="email">
              <el-input v-model="form.email" size="medium" placeholder="请填写邮箱"/>
            </el-form-item>
            <el-form-item class="submit">
              <el-button type="primary" @click="submitForm('editForm')">保 存</el-button>
              <el-button @click="resetForm('editForm')">重 置</el-button>
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import UserModel from '@/models/user'
export default {
  name: 'UserAdd',
  components: {},
  props: {
    editID: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      loading: false,
      form: {
        auth: 1,
        username: '',
        realname: '',
        mobile: '',
        email: '',
        company_id: '',
        password: ''
      },
    }
  },
  computed: {},
  created() { },
  mounted() {
    this.getUserInfo()
  },
  methods: {
    async getUserInfo() {
      this.form = await UserModel.getUserInfo(this.editID)
      // this.form.password = ''
    },
    hanleHide() {
      this.$emit('hanleHide', false)
    },
    async submitForm() {
      await UserModel.editOne(this.editID, this.form)
      this.$message.success('修改用户信息成功')
      this.hanleHide()
    },
    // 重置表单
    resetForm(formName) {
      this.getUserInfo()
    },
  },
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .el-divider--horizontal {
    margin: 0;
  }
  .container {
    height: 100%
    .header {
      display flex
      justify-content space-between
      align-items center
      .title {
        height 59px
        line-height 59px
        font-size 16px
        font-weight 500
      }
      .back {
        margin-right 40px
        cursor pointer
      }
    }
    .wrap {
      padding: 20px;
    }
  }
</style>
